.layout {
  min-height: 80vh;

  & a {
    &:link, &:visited {
      color: @black;
    }

    &:hover, &:active {
      color: @primary-color;
    }
  }

  &>li {
    margin-bottom: 18px;
  }
}
@primary-color: #00adb5;@black: #222831;@white: #fff;@cyan: #DEEAF3;@gray: #888;@dark-gray: #393e46;@blue: #001529;